.catalog, .catalog-section, .catalog-brand, .product-detail {
  lost-center: 980px;
  flex-wrap: wrap;
  margin-top: 8rem;
  padding: 0 3rem;
  @media all and (min-width: $bp-med) {
    margin-top: 15rem;
    padding: 0; }

  .one-block {
    @media all and (min-width: $bp-med) {
      lost-masonry-column: 4/12;
      margin-bottom: 30px; } }
  .two-block {
    @media all and (min-width: $bp-med) {
      lost-masonry-column: 8/12;
      margin-bottom: 30px; } } }
.inner-block {
  position: relative;
  width: 100%;
  margin-bottom: .5rem;
  a {
    display: flex;
    flex-flow: row wrap;
    border-bottom: none;
    outline: 0;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0); }
  img {
    overflow: hidden;
    width: 100%;
    height: 10rem;
    object-fit: cover;
    @include object-fit(cover); }
  .block-box {
    color: $bgc;
    width: 100%;
    height: auto;
    padding: 1rem;
    .title {
      margin-bottom: .5rem;
      font-size: responsive 1rem 1.5rem;
      font-weight: $bold;
      line-height: 1; }
    .desc {
      font-size: responsive .8rem .9rem; }
    .btn-box {
      position: absolute;
      top: 61%;
      right: .5rem;
      padding: .25rem .7rem;
      box-shadow: -4px -4px 20px rgba($black, .2);
      @media all and (min-width: $bp-med) {
        top: 58%; } } } }

.ropa {
  .block-box, .section-box, .brand-box {
    background-color: $green;
    .btn-box {
      background-color: darken($green, 15%); } } }
.artesania {
  .block-box, .section-box, .brand-box {
    background-color: $yellow;
    .btn-box {
      background-color: darken($yellow, 15%); } } }
.arte {
  .block-box, .section-box, .brand-box {
    background-color: $red;
    .btn-box {
      background-color: darken($red, 15%); } } }
.comida {
  .block-box, .section-box, .brand-box {
    background-color: $purple;
    .btn-box {
      background-color: darken($purple, 15%); } } }
.maquillaje {
  .block-box, .section-box, .brand-box {
    background-color: $pinky;
    .btn-box {
      background-color: darken($pinky, 15%); } } }
.decoracion {
  .block-box, .section-box, .brand-box {
    background-color: $turk;
    .btn-box {
      background-color: darken($turk, 15%); } } }
.varios {
  .block-box, .section-box, .brand-box {
    background-color: $bordo;
    .btn-box {
      background-color: darken($bordo, 15%); } } }

.catalog-section, .brand-info {
  h1 {
    display: block;
    margin-bottom: .5rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid darken($bgc, 15%);
    color: $darkblue;
    @media all and (min-width: $bp-med) {
      margin-bottom: 2rem;
      padding-bottom: 1rem; } } }

.catalog-section {
  padding: 0 3rem;
  @media all and (min-width: $bp-med) {
    padding: 0; } }

.sections-menu {
  @media all and (min-width: $bp-med) {
    lost-column: 2/12; }
  ul {
    text-align: center;
    li {
      position: relative;
      display: inline-block;
      margin-right: 1rem;
      margin-bottom: 1rem;
      @media all and (min-width: $bp-med) {
        display: block;
        text-align: right;
        margin-right: 0; }
      .current-sec {
        font-weight: $normal;
        pointer-events: none;
        cursor: default;
        color: $darkblue;
        &::after {
          content: '';
          position: absolute;
          right: 0;
          display: block;
          margin: auto;
          height: 2px;
          width: 0px;
          margin-top: 5px;
          width: 100%;
          background: $darkblue; } }
      a {
        position: relative;
        font-size: responsive .7rem .9rem;
        font-weight: $light;
        border: none;
        color: $darkblue;
        &::after {
          content: '';
          position: absolute;
          right: 0;
          display: block;
          margin: auto;
          height: 2px;
          width: 0px;
          margin-top: 5px;
          background: transparent;
          transition: width .5s ease, background-color .5s ease; }
        &:hover::after {
          width: 100%;
          background: $darkblue; }
        &.current {
          &::after {
            display: block;
            width: 100%;
            height: 1px;
            margin-top: 10px; } } } } } }

.sections-list {
  @media all and (min-width: $bp-med) {
    lost-column: 10/12; } }

.catalog-section {
  .section-block {
    position: relative;
    margin-bottom: .5rem;
    @media all and (min-width: $bp-med) {
      lost-column: 1/2;
      margin-bottom: 30px; }
    img {
      overflow: hidden;
      width: 100%;
      height: 10rem;
      object-fit: cover;
      @include object-fit(cover);
      @media all and (min-width: $bp-med) {
        height: 20rem; } }
    .section-box {
      @include centerer;
      padding: 1rem;
      text-transform: uppercase;
      text-align: center;
      font-size: responsive .8rem 1rem;
      color: $bgc; } } }

.brand-info {
  @media all and (min-width: $bp-med) {
    lost-column: 4/12; }
  h1 {
    border-bottom: none;
    span {
      display: block;
      margin-top: .5rem;
      font-size: responsive .8rem 1rem;
      a {
        color: $darkblue;
        border-bottom: 2px solid $darkblue; } } } }

.brand-list {
  margin-top: 1rem;
  @media all and (min-width: $bp-med) {
    lost-column: 8/12;
    margin-top: 0; }
  .brand-block {
    @media all and (min-width: $bp-med) {
      lost-column: 1/2;
      margin-bottom: 30px; }
    img {
      overflow: hidden;
      width: 100%;
      height: 15rem;
      object-fit: cover;
      @include object-fit(cover);
      @media all and (min-width: $bp-med) {
        height: 20rem; } }
    .inner-block {
      .brand-box {
        @include centerer;
        position: absolute;
        padding: .5rem 1rem;
        text-align: center; }
      a {
        border: none;
        color: $bgc; }
      img {
        margin-bottom: 1rem; } } } }

.product-block {
  @media all and (min-width: $bp-med) {
    lost-column: 8/12; } }

.product-info {
  @media all and (min-width: $bp-med) {
    lost-column: 4/12; }
  h1 {
    margin-bottom: .5rem;
    color: $darkblue; }
  .breadcrumb {
    display: flex;
    margin-bottom: .5rem;
    font-size: responsive .7rem .9rem;
    li {
      margin-right: .5rem;
      &::after {
        content: '>';
        margin-left: .5rem;
        color: $darkblue; }
      &:last-child {
        &::after {
          content: '';
          display: none; } }
      a {
        color: $darkblue;
        border: none; } } }
  .product-price {
    display: block;
    margin-top: 1rem;
    padding-top: 1rem;
    border-top: 1px solid darken($bgc, 15%);
    color: $darkblue;
    font-weight: $bold;
    font-size: responsive 1.3rem 1.8rem; }
  .share-product {
    margin-top: 1rem;
    span {
      display: inline-block;
      float: left;
      margin-right: 1rem;
      padding-top: .35rem;
      text-transform: uppercase;
      font-size: responsive .6rem .8em;
      color: $darkblue; }
    a {
      border: none; }
    svg {
      width: 28px;
      fill: darken($bgc, 25%);
      transition: all .5s ease;
      &:hover,
      &:focus {
        fill: $turk; } }
    ul {
      display: flex;
      li {
        margin-right: .5rem; } } } }

.unslider-wrap {
  li {
    width: 100%;
    height: 15rem;
    overflow: hidden;
    @media all and (min-width: $bp-med) {
      height: 30rem; }
    img {
      overflow: hidden;
      width: 100%;
      height: 15rem;
      object-fit: cover;
      @include object-fit(cover);
      @media all and (min-width: $bp-med) {
        height: 30rem; } } } }

.unslider-nav {
  margin-top: 1rem; }
